* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body {
  background: url("./background.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.header {
  text-align: center;
  padding-top: 20px;
  font-size: 40px;
  background-color: rgba(235, 235, 235, 0.78);
}

.scoreBox {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 30px;
  display: flex;
  justify-content: center;
  background-color: rgba(235, 235, 235, 0.78);
}

.cardContainer {
  display: flex;
  flex-wrap: wrap;
  width: 1200px;
  justify-content: center;
  margin: auto;
  gap: 100px;
  margin-top: 20px;
}
.card {
  text-align: center;
  line-height: 3;
  background-color: rgba(212, 156, 247, 0.78);
  padding: 10px;
  border-radius: 10px;
}

.card:hover {
  cursor: pointer;
  background-color: rgba(250, 60, 60, 0.78);
  transition: ease 0.4s;
}
.image {
  width: 200px;
  height: 200px;
}
